import React, { useEffect, useRef } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { Card, CardBody } from '_metronic/_partials/controls';
import ProductUnitDetailForm from './ProductUnitDetailForm';
import { fetchUnitById, updateUnit } from '../../../product-redux/unitSlice';
import Loading from 'app/components/Loading';
import { dialog } from 'app/components/DialogNotify';
import TopHeader from 'app/modules/Product/components/TopHeader';
import { injectIntl } from 'react-intl';

function ProductUnitDetailPage({
    history,
    match: {
        params: { id }
    },
    intl
}) {
    const dispatch = useDispatch();
    const _ = require('lodash');
    const unit = useSelector(state => state.product.unit);
    const { unitDetail, loading } = unit;

    useEffect(() => {
        dispatch(fetchUnitById(id));
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id]);

    const btnRef = useRef();

    const handleEditClick = () => {
        if (btnRef && btnRef.current) {
            btnRef.current.click();
        }
    };

    const handleSave = values => {
        let body = {
            id: id,
            params: { ..._.omit(values, ['id']) }
        };
        dispatch(updateUnit(body)).then(res => {
            if (res.type.includes('fulfilled')) {
                dialog.success(
                    `${intl.formatMessage({
                        id: 'PRODUCT.DETAIL.UNIT.SUCCESS'
                    })}`
                );
            } else {
                dialog.error(
                    `${intl.formatMessage({
                        id: 'PRODUCT.DETAIL.UNIT.FAIL'
                    })}`
                );
            }
        });
    };

    const initUnit = {
        id: unitDetail?.id || '',
        name: unitDetail?.name || ''
    };

    return (
        <>
            {loading && <Loading />}
            <TopHeader
                title={intl.formatMessage({
                    id: 'UNIT.DETAIL.TITLE'
                })}
            >
                <button
                    type="button"
                    onClick={() => {
                        history.push('/product/unit');
                    }}
                    className="btn btn-light"
                >
                    <i className="fa fa-arrow-left"></i>
                    {intl.formatMessage({
                        id: 'GLOBAL.BUTTON.CANCEL'
                    })}
                </button>
                {`  `}
                {`  `}
                <button
                    type="submit"
                    className="btn btn-primary ml-2"
                    onClick={handleEditClick}
                >
                    {intl.formatMessage({
                        id: 'GLOBAL.BUTTON.SAVE'
                    })}
                </button>
            </TopHeader>
            <div className="pb-8 px-8">
                <Card>
                    <CardBody>
                        <ProductUnitDetailForm
                            onSave={handleSave}
                            btnRef={btnRef}
                            initialValues={initUnit}
                            intl={intl}
                        ></ProductUnitDetailForm>
                    </CardBody>
                </Card>
            </div>
        </>
    );
}

export default injectIntl(connect(null, null)(ProductUnitDetailPage));
