import { FastField, Form, Formik } from 'formik';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { Input, Select } from '_metronic/_partials/controls';
import * as Yup from 'yup';
import ReactTagInput from '@pathofdev/react-tag-input';
import '@pathofdev/react-tag-input/build/index.css';
import './index.scss';
import { Route } from 'react-router-dom';
import DialogUploadImgageProduct from './image-card/DialogUploadImgageProduct';
import { IMAGES } from 'constant/Images';
import { useFileUpload } from 'use-file-upload';

ProductDetailForm.propTypes = {
    initialValues: PropTypes.object,
    unitList: PropTypes.array,
    taxList: PropTypes.array,
    originList: PropTypes.array,
    onChangeTag: PropTypes.func,
    imageProduct: PropTypes.string,
    idProduct: PropTypes.string,
    onUploadImageUrl: PropTypes.func,
    onUploadImageFile: PropTypes.func
};

function ProductDetailForm({
    intl,
    initialValues = {},
    btnRef,
    onSave,
    unitList,
    taxList,
    originList,
    onChangeTag,
    imageProduct = '',
    idProduct = '',
    onUploadImageUrl,
    onUploadImageFile
}) {
    const ProductUpdateSchema = Yup.object().shape({
        price: Yup.number().required(
            ` ${intl.formatMessage({
                id: 'PRODUCT.CREATE.SCHEMA.PRICE_REQUIRED'
            })}`
        )
    });
    const [tags, setTags] = useState(null);
    const listIngredient = initialValues?.ingredients;
    const arrayIngridient = listIngredient.split(',');
    const [files, selectFiles] = useFileUpload();

    const handleUpLoadImageFile = e => {
        selectFiles({
            accept: 'image/*'
        });
    };

    useEffect(() => {
        if (files) onUploadImageFile(files);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [files]);
    return (
        <>
            <Route path="/product/:id/detail/upload-image">
                {({ history, match }) => (
                    <DialogUploadImgageProduct
                        id={match && match.params.id}
                        show={match != null}
                        onHide={() =>
                            history.push(`/product/${idProduct}/detail`)
                        }
                    />
                )}
            </Route>
            <Formik
                enableReinitialize={true}
                initialValues={(initialValues && initialValues) || {}}
                onSubmit={onSave}
                validationSchema={ProductUpdateSchema}
            >
                {({ handleSubmit }) => (
                    <>
                        <Form className="form form-label-right">
                            <div className="row">
                                <div className="col-lg-4 col-md-4">
                                    <div className="image-product-detail">
                                        <img
                                            style={{
                                                width: 'inherit',
                                                height: '100%',
                                                objectFit: 'cover'
                                            }}
                                            src={
                                                files?.source ||
                                                imageProduct ||
                                                IMAGES.NOT_FOUND
                                            }
                                            alt=""
                                        />
                                    </div>
                                    <div
                                        className="col-lg-12 col-md-12"
                                        style={{
                                            padding: 'unset',
                                            marginTop: '1rem'
                                        }}
                                    >
                                        <div className="input-group mb-3 ">
                                            <div className="input-group-prepend">
                                                <span
                                                    type="button"
                                                    className="input-group-text"
                                                    id="basic-addon3"
                                                    style={{
                                                        backgroundColor:
                                                            '#3699FF',
                                                        color: '#ffffff'
                                                    }}
                                                    data-toggle="tooltip"
                                                    data-placement="top"
                                                    title="Click me to change image!"
                                                    onClick={
                                                        handleUpLoadImageFile
                                                    }
                                                >
                                                    Upload
                                                </span>
                                            </div>
                                            <input
                                                type="text"
                                                className="form-control"
                                                id="basic-url"
                                                aria-describedby="basic-addon3"
                                                onChange={onUploadImageUrl}
                                                placeholder={'Url image'}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-8 col-md-8">
                                    <div className="form-group row align-items-center">
                                        <div className="col-3">
                                            <span className="order-title">
                                                {intl.formatMessage({
                                                    id:
                                                        'PRODUCT.TOPFILTER.JANCODE'
                                                })}
                                            </span>
                                        </div>
                                        <div className="col-9">
                                            <FastField
                                                name="id"
                                                component={Input}
                                                disabled={true}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group row align-items-center">
                                        <div className="col-3">
                                            <span className="order-title">
                                                {intl.formatMessage({
                                                    id: 'PRODUCT.TOPFILTER.NAME'
                                                })}
                                            </span>
                                        </div>
                                        <div className="col-9">
                                            <FastField
                                                name="name"
                                                component={Input}
                                                placeholder={intl.formatMessage(
                                                    {
                                                        id:
                                                            'PRODUCT.CREATE.PLACEHOLER.NAME'
                                                    }
                                                )}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group row align-items-center">
                                        <div className="col-3">
                                            <span className="order-title">
                                                {intl.formatMessage({
                                                    id:
                                                        'PRODUCT.TOPFILTER.PRICE'
                                                })}
                                            </span>
                                        </div>
                                        <div className="col-9">
                                            <FastField
                                                name="price"
                                                component={Input}
                                                placeholder={intl.formatMessage(
                                                    {
                                                        id:
                                                            'PRODUCT.CREATE.PLACEHOLER.PRICE'
                                                    }
                                                )}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group row align-items-center">
                                        <div className="col-3">
                                            <span className="order-title">
                                                {intl.formatMessage({
                                                    id:
                                                        'PRODUCT.TOPFILTER.ORIGIN'
                                                })}
                                            </span>
                                        </div>
                                        <div className="col-9">
                                            <Select name="origin_id">
                                                {originList?.map(origin => (
                                                    <option
                                                        key={origin?.id}
                                                        value={origin?.id}
                                                    >
                                                        {origin?.name}
                                                    </option>
                                                ))}
                                            </Select>
                                        </div>
                                    </div>
                                    <div className="form-group row align-items-center">
                                        <div className="col-3">
                                            <span className="order-title">
                                                {intl.formatMessage({
                                                    id: 'PRODUCT.TOPFILTER.UNIT'
                                                })}
                                            </span>
                                        </div>
                                        <div className="col-9">
                                            <Select name="unit_id">
                                                {unitList?.map(unit => (
                                                    <option
                                                        key={unit.id}
                                                        value={unit.id}
                                                    >
                                                        {unit.name}
                                                    </option>
                                                ))}
                                            </Select>
                                        </div>
                                    </div>
                                    <div className="form-group row align-items-center">
                                        <div className="col-3">
                                            <span className="order-title">
                                                {intl.formatMessage({
                                                    id: 'PRODUCT.TOPFILTER.TAX'
                                                })}
                                            </span>
                                        </div>
                                        <div className="col-9">
                                            <Select name="tax_id">
                                                {taxList?.map(tax => (
                                                    <option
                                                        key={tax.id}
                                                        value={tax.id}
                                                    >
                                                        {tax.name}
                                                    </option>
                                                ))}
                                            </Select>
                                        </div>
                                    </div>
                                    <div className="form-group row">
                                        <div className="col-3">
                                            <span className="order-title">
                                                {intl.formatMessage({
                                                    id:
                                                        'PRODUCT.TOPFILTER.INGREDIENT'
                                                })}
                                            </span>
                                        </div>
                                        <div className="col-9 product-info-container">
                                            <ReactTagInput
                                                tags={tags || arrayIngridient}
                                                placeholder={intl.formatMessage(
                                                    {
                                                        id:
                                                            'PRODUCT.CREATE.PLACEHOLER.INGREDIENT'
                                                    }
                                                )}
                                                editable={true}
                                                readOnly={false}
                                                allowDuplicates={false}
                                                onChange={newTag => {
                                                    setTags(newTag);
                                                    onChangeTag(newTag);
                                                }}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <button
                                type="submit"
                                style={{ display: 'none' }}
                                ref={btnRef}
                                onSubmit={() => handleSubmit()}
                            ></button>
                        </Form>
                    </>
                )}
            </Formik>
        </>
    );
}

export default ProductDetailForm;
